import { call, put, takeEvery, all } from "redux-saga/effects";
import axios from "axios";
import { success, failed } from "./lyrics";

const INSERT_DATA = "sagas/INSERT_DATA"; // 이 액션으로 체킹해서 호출
const UPDATE_DATA = "sagas/UPDATE_DATA";
const DELETE_DATA = "sagas/DELETE_DATA";

export const insertData = () => ({ type: INSERT_DATA });
export const updateData = () => ({ type: UPDATE_DATA });
export const deleteData = () => ({ type: DELETE_DATA });

// dispatch => checking =>
function* getAsyncData(action) {
  console.log("호출성공", action);
  try {
    const data = yield axios.put("http://localhost:3001/api/insert");
    console.log(data);
    yield put(success());
  } catch (error) {
    console.error(error);
    yield put(failed());
  }
}

function* testSaga() {
  yield takeEvery(GET_ASYNCDATA, getAsyncData);
}

export function* rootSaga() {
  yield all([testSaga()]);
}

export default rootSaga;
